.Header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .5rem 2rem;
    font-size: 1rem;
    line-height: 1;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 3;
    background: #dedede;
    a:visited {
        color: inherit; }
    div,
    p,
    span,
    button,
    a, {
        user-select: none; }
    .rotate,
    .print, {
        margin-right: 1rem; }

    .rotate {
        height: calc( 100% - 2px ); }
    .scale {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 5rem;
        .inputWrapper {
            position: relative;
            line-height: 100%;
            pointer-events: none;
            &::after {
                content: '%';
                position: absolute;
                right: 0;
                top: 50%;
                font-size: 1em;
                transform: translate(4px, -50%); }
            .input {
                pointer-events: all;
                font-size: 1em; } }
        .scaleBtn {
            position: relative;
            line-height: 0;
            box-sizing: border-box;
            border-radius: 50%;
            border: 1px solid #000;
            height: 1rem;
            width: 1rem;
            padding: 0;

            &.minus::before,
            &.plus::before,
            &.plus::after, {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                line-height: 0;
                content: "";
                width: 45%;
                height: 1px;
                background: #000; }

            &.plus::after {
                transform: translate(-50%, -50%) rotate(90deg); } } }
    .pages {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        width: 10%;
        flex-grow: 1;
        .slash {
            margin: 0 .2rem; }
        .pageCurrent {
            font-size: .9rem; } }
    .buttonsWrapper {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        width: 10%;
        flex-grow: 1; }
    .iconWrapper {
        width: 1.5rem;
        height: 1rem;
        cursor: pointer;
        svg {
            width: 100%;
            height: 100%;
            path {
                fill: #000; } }
        &:active {
            opacity: .5; } } }

@media screen and ( max-width: 400px ) {
    .Header {
        padding: 0.5rem 1rem;
        .buttonsWrapper {
            flex-grow: 1; } } }

@media screen and ( max-width: 310px ) {
    html {
        font-size: 14px; }

    .Header {
        .rotate,
        .print, {
            margin-right: .5rem; }
        .scale {
            flex-grow: 1;
            width: 10%; } } }

@media screen and ( max-width: 235px ) {
    html {
        font-size: 11px; }
    .Header {
        .scale {
            .scaleBtn {
                &.minus::before,
                &.plus::before,
                &.plus::after {
                    display: none; } }
            .inputWrapper {
                .input {
                    text-align: center; }
                &::after {
                    content: ""; } } } } }

@media screen and ( max-width: 155px ) {
    html {
        font-size: 8px; }
    .Header {
        padding: 3px; } }

@media screen and ( max-width: 100px ) {
    body {
        visibility: hidden; } }

@media screen and ( min-width: 1920px ) {
    html {
        font-size: 1vw; } }
